import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { NoticeComponent } from './notice.component';
import { NoticeSetupComponent } from './components/notice-setup/notice-setup.component';
import { NoticeReportsComponent } from './components/notice-reports/notice-reports.component';

const routes: Routes = [

	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: NoticeSetupComponent},
	{ path: 'reports', component: NoticeReportsComponent},
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NoticeRoutingModule { }
